$font-primary:       '../fonts/Roboto_Mono' !default;
$font-secondary:     '../fonts/Rubik' !default;

@font-face {
    font-family: 'RobotoMono-Bold';
    src: url('#{$font-primary}/RobotoMono-Bold.ttf') format('truetype');
    // src: url('#{$font-primary}/crimsontext-bold-webfont.woff2') format('woff2'),
    // url('#{$font-primary}/crimsontext-bold-webfont.woff') format('woff');
    // font-weight: bold;
    // font-style: normal;
}

@font-face {
    font-family: 'RobotoMono-BoldItalic';
    src: url('#{$font-primary}/RobotoMono-BoldItalic.ttf') format('truetype');
    // src: url('#{$font-primary}/crimsontext-bold-webfont.woff2') format('woff2'),
    // url('#{$font-primary}/crimsontext-bold-webfont.woff') format('woff');
    // font-weight: bold;
    // font-style: normal;
}

@font-face {
    font-family: 'RobotoMono-Italic';
    src: url('#{$font-primary}/RobotoMono-Italic.ttf') format('truetype');
    // font-weight: bold;
    // font-style: normal;
}

@font-face {
    font-family: 'RobotoMono-Light';
    src: url('#{$font-primary}/RobotoMono-Light.ttf') format('truetype');
    // font-weight: bold;
    // font-style: normal;
}

@font-face {
    font-family: 'RobotoMono-LightItalic';
    src: url('#{$font-primary}/RobotoMono-LightItalic.ttf') format('truetype');
    // font-weight: bold;
    // font-style: normal;
}
@font-face {
    font-family: 'RobotoMono-Regular';
    src: url('#{$font-primary}/RobotoMono-Regular.ttf') format('truetype');
    // font-weight: bold;
    // font-style: normal;
}



@font-face {
  font-family: 'Rubik-Black';
  src: url('#{$font-secondary}/Rubik-Black.ttf') format('truetype');
  // src: url('#{$font-secondary}/lato-bold-webfont.woff2') format('woff2'),
  // url('#{$font-secondary}/lato-bold-webfont.woff') format('woff');
  // font-weight: 700;
  // font-style: normal;
}

@font-face {
  font-family: 'Rubik-Bold';
  src: url('#{$font-secondary}/Rubik-Bold.ttf') format('truetype');
  // src: url('#{$font-secondary}/lato-bold-webfont.woff2') format('woff2'),
  // url('#{$font-secondary}/lato-bold-webfont.woff') format('woff');
  // font-weight: 700;
