}

@font-face {
  font-family: 'Rubik-Regular';
  src: url('#{$font-secondary}/Rubik-Regular.ttf') format('truetype');
  // font-weight: bold;
  // font-style: normal;
}

@font-face {
  font-family: 'Rubik-Light';
  src: url('#{$font-secondary}/Rubik-Light.ttf') format('truetype');
  // font-weight: bold;
  // font-style: normal;
}



